const   HEADER_SEARCH_FORM = document.querySelector('#headerSearchForm'),
        HEADER_SEARCH_BTN = document.querySelector('#headerSearchBtn'),
        HEADER_SEARCH_BOX = document.querySelector('.header__search-box'),
        HEADER_SEARCH_FIELD = document.querySelector('.header__search-box input')

let clickState = false,
    inputState = false

const hideSearchBox = () => {
    if(HEADER_SEARCH_FIELD !== document.activeElement && !HEADER_SEARCH_BOX.matches(':hover')) {
        HEADER_SEARCH_BTN.classList.remove('btn--focused')
        HEADER_SEARCH_BOX.classList.remove('header__search-box--focused')
        HEADER_SEARCH_BOX.classList.remove('is-invalid')
    }
}

const showSearchBox = () => {
    HEADER_SEARCH_BTN.classList.add('btn--focused')
    HEADER_SEARCH_BOX.classList.add('header__search-box--focused')
}

const searchFieldValidate = () => {
    return HEADER_SEARCH_FIELD.value.length < 3 ? false : true
}

const handleSearchBtnClick = () => {
    if (clickState === false) {
        clickState = true
        handleSearchBtnClick()
        if (!HEADER_SEARCH_BOX.classList.contains('header__search-box--focused')) {
            showSearchBox()
        } 
    } else {
        clickState = false
            if (inputState === true || (searchFieldValidate() && HEADER_SEARCH_BOX.classList.contains('header__search-box--focused'))) {
                if (searchFieldValidate()) {
                    HEADER_SEARCH_FORM.submit()
                } else {
                    HEADER_SEARCH_BOX.classList.add('is-invalid')
                }
            }
    }
}

HEADER_SEARCH_BTN.addEventListener('mouseover', () => {
    setTimeout(showSearchBox, 100) 
})

HEADER_SEARCH_BTN.addEventListener('mouseleave', () => {
    setTimeout(hideSearchBox, 100)   
})

HEADER_SEARCH_BTN.addEventListener('mouseup', () => {
    handleSearchBtnClick()
})

HEADER_SEARCH_BOX.addEventListener('mouseleave', () => {
    hideSearchBox()
})

HEADER_SEARCH_FIELD.addEventListener('focusin', () => {
    inputState = true
})

HEADER_SEARCH_FIELD.addEventListener('focusout', () => {
    hideSearchBox()
    if (!HEADER_SEARCH_BOX.matches(':hover') && !HEADER_SEARCH_BTN.matches(':hover')) {
        inputState = false
    }
})